@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:700&display=swap);
/*
@font-face {
    font-family: 'egg';
    src:url('../lib/assets/fonts/egg.woff');
}
*/

:root {
  --white:             rgba(255, 255, 255, 1);
  --purple:              rgba(59, 11, 251, 1);
  --grey:             rgba(59, 11, 251, 0.05);
  --paddingDefault:                        50px; }

.button1, .button2, form div.inputContainer input {
  -webkit-transition-duration: 400ms;
          transition-duration: 400ms; }

.flex, header, form {
  display: flex; }

.jc-sa {
  justify-content: space-around; }

.jc-sb, header, .recent .itemRecent {
  justify-content: space-between; }

.alignXY, .alignY, .alignX {
  position: relative; }

.alignXY {
  top: 50%;
  bottom: auto;
  left: 50%;
  right: auto;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.alignY {
  top: 50%;
  bottom: auto;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%); }

.alignX {
  left: 50%;
  right: auto;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0); }

h1, h2, h3, h4, h5, h6, p, span, input, button {
  font-family: "Roboto Mono", monospace;
  color: rgba(59, 11, 251, 1);
  color: var(--purple);
  font-size: 16.5px; }

.button1, .button2 {
  display: block;
  width: 225px;
  padding: 20px;
  margin-top: 12px;
  border: none;
  border-radius: 12px;
  text-align: center;
  cursor: pointer; }
  .button1:hover, .button2:hover {
    opacity: 0.35; }

.button1 {
  background: rgba(59, 11, 251, 1);
  background: var(--purple);
  color: rgba(255, 255, 255, 1);
  color: var(--white); }

.button2 {
  background: rgba(59, 11, 251, 0.05);
  background: var(--grey);
  color: rgba(59, 11, 251, 1);
  color: var(--purple); }

html, body {
  width: 100%;
  height: 100%;
  min-height: 600px;
  scroll-behavior: smooth; }

html {
  background: rgba(255, 255, 255, 1);
  background: var(--white); }

::-webkit-input-placeholder {
  opacity: 0.5;
  color: rgba(59, 11, 251, 1);
  color: var(--purple); }

::-moz-placeholder {
  opacity: 0.5;
  color: rgba(59, 11, 251, 1);
  color: var(--purple); }

:-ms-input-placeholder {
  opacity: 0.5;
  color: rgba(59, 11, 251, 1);
  color: var(--purple); }

::-ms-input-placeholder {
  opacity: 0.5;
  color: rgba(59, 11, 251, 1);
  color: var(--purple); }

::placeholder {
  opacity: 0.5;
  color: rgba(59, 11, 251, 1);
  color: var(--purple); }

.fer-appear {
  top: 75px;
  opacity: 0.01; }

.fer-appear.fer-appear-active {
  top: 0px;
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in; }

header {
  align-items: center;
  flex-flow: row nowrap;
  width: 100%;
  height: 105px;
  padding: 0px 50px;
  padding: 0px var(--paddingDefault); }
  header h1 {
    font-size: 30px; }
  header h2 {
    font-size: 20px; }

div.balance, div.action {
  padding-bottom: 20px; }

form {
  align-items: center;
  flex-flow: column nowrap; }
  form div.inputContainer {
    position: relative;
    width: 50%;
    max-width: 400px;
    margin-bottom: 10px; }
    form div.inputContainer input {
      background: none;
      width: 100%;
      padding: 14px 30px;
      color: rgba(59, 11, 251, 1);
      color: var(--purple);
      border: 3px solid rgba(59, 11, 251, 0.05);
      border: 3px solid var(--grey);
      border-radius: 12px; }
      form div.inputContainer input:hover {
        background-color: rgba(59, 11, 251, 0.05);
        background-color: var(--grey); }
      form div.inputContainer input:focus {
        background-color: rgba(59, 11, 251, 0.05);
        background-color: var(--grey);
        border-color: rgba(59, 11, 251, 0.05);
        border-color: var(--grey); }
    form div.inputContainer span {
      display: block;
      position: absolute;
      right: 40px;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%); }

.recent {
  padding: 0px 50px;
  padding: 0px var(--paddingDefault); }
  .recent .itemRecent {
    display: inline-flex;
    flex-flow: row nowrap;
    position: relative;
    margin-top: 20px;
    padding: 60px 50px;
    padding: 60px var(--paddingDefault);
    width: 100%;
    background: rgba(59, 11, 251, 0.05);
    background: var(--grey);
    border-radius: 20px; }

h2.nbBalance {
  text-align: right;
  padding: 0px 50px;
  padding: 0px var(--paddingDefault);
  font-size: 50px; }

@media screen and (orientation: portrait) {
  .desktop {
    display: none; }
  :root {
    --paddingDefault: 30px; }
  header {
    height: 80px; }
    header h1, header h2 {
      font-size: 25px; }
  form div.inputContainer {
    width: 90%; }
  div.balance, div.action {
    padding-bottom: 65px; }
  .recent {
    padding: 0px 50px;
    padding: 0px var(--paddingDefault); }
    .recent .itemRecent {
      flex-flow: column nowrap; }
      .recent .itemRecent h5 {
        text-align: center;
        padding-bottom: 10px; } }

@media screen and (orientation: landscape) {
  .mobile {
    display: none; } }

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none; }

a:link {
  text-decoration: none; }

